/* Estilos para o formulário de edição de alunos */
.editar-aluno-form-container {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin-top: 20px;
  width: 100%;
}

.editar-aluno-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.form-row {
  display: flex;
  gap: 16px;
  flex-wrap: wrap;
}

.form-group {
  flex: 1;
  min-width: 240px;
}

.form-group.full-width {
  flex: 1 1 100%;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #333;
}

.form-group input,
.form-group select,
.form-group textarea {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 14px;
  transition: border-color 0.3s;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  border-color: #2684ff;
  outline: none;
  box-shadow: 0 0 0 2px rgba(38, 132, 255, 0.2);
}

.form-group textarea {
  resize: vertical;
  min-height: 80px;
}

.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 20px;
}

.btn-cancelar {
  background-color: #f2f2f2;
  color: #333;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px 16px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.btn-salvar {
  background-color: #2684ff;
  color: white;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 14px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-weight: bold;
}

.btn-cancelar:hover {
  background-color: #e5e5e5;
}

.btn-salvar:hover {
  background-color: #1a6fc9;
}

.btn-salvar:disabled {
  background-color: #a0c4f7;
  cursor: not-allowed;
}

.mensagem-sucesso {
  background-color: #e6f7e6;
  color: #2e7d32;
  padding: 12px 16px;
  border-radius: 4px;
  margin-bottom: 16px;
  border-left: 4px solid #2e7d32;
}

.mensagem-erro {
  background-color: #ffebee;
  color: #c62828;
  padding: 12px 16px;
  border-radius: 4px;
  margin-bottom: 16px;
  border-left: 4px solid #c62828;
}

/* Responsividade */
@media (max-width: 768px) {
  .form-row {
    flex-direction: column;
    gap: 12px;
  }

  .form-group {
    min-width: 100%;
  }

  /* Melhoria para botões em tablets */
  .form-actions {
    justify-content: space-between;
    margin-top: 24px;
  }

  .btn-cancelar,
  .btn-salvar {
    padding: 12px 16px;
    font-size: 16px;
    min-width: 120px;
  }
}

/* Melhorias específicas para dispositivos móveis */
@media (max-width: 480px) {
  .editar-aluno-form-container {
    padding: 15px;
    margin-top: 10px;
  }

  .form-group label {
    font-size: 16px;
  }

  .form-group input,
  .form-group select,
  .form-group textarea {
    padding: 12px;
    font-size: 16px; /* Facilita a leitura e toque em telas pequenas */
  }

  /* Botões em tamanho adequado para toques */
  .form-actions {
    flex-direction: column;
    gap: 16px;
    margin-top: 25px;
    margin-bottom: 10px;
  }

  .btn-cancelar,
  .btn-salvar {
    width: 100%;
    padding: 16px;
    font-size: 18px;
    border-radius: 6px;
    margin: 0; /* Remove margens para ocupar todo o espaço */
  }

  .btn-salvar {
    order: -1; /* Coloca o botão salvar acima do cancelar */
    font-weight: bold;
  }

  /* Melhorias nas mensagens */
  .mensagem-sucesso,
  .mensagem-erro {
    padding: 16px;
    font-size: 16px;
    text-align: center;
    margin-bottom: 20px;
  }
}

.checkbox-group {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 5px;
}

.checkbox-label {
  display: flex;
  align-items: center;
  font-size: 14px;
  cursor: pointer;
}

.checkbox-label input[type="checkbox"] {
  margin-right: 8px;
  width: 16px;
  height: 16px;
  accent-color: #007bff;
}

@media (max-width: 768px) {
  .checkbox-group {
    margin-bottom: 15px;
  }

  .checkbox-label {
    font-size: 16px;
    padding: 8px 0;
  }

  .checkbox-label input[type="checkbox"] {
    width: 20px;
    height: 20px;
    margin-right: 10px;
  }
}
